@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('https://www.wallpapertip.com/wmimgs/11-112111_wallpaper-grey-blue-gradient-linear-light-blue-gray.jpg');
  background-size: cover;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 40px 70px;
  }
  
  .card-style{
    margin: 30px 15px;
    width: calc( 25% - 32px );
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    border-radius: 7px;
    cursor: pointer;
    border: 1px solid transparent;
  }
  
  .card-img{
    width: 100%;
    height: 380px;
    border-top-right-radius: 7px;
    border-top-left-radius: 7px;
  }
  
  .card-title{
    font-size: 18px;
    margin: 5px 0 10px 0;
    text-align: center;
    text-shadow: 3px 4px 7px rgba(81,67,21,0.8);
    font-weight: 500;
  }
  
  .card-style:hover{
    -webkit-transform: scale(1.05,1.05);
            transform: scale(1.05,1.05);
  }
  
  .active{
    background-color: tomato;
  }
.heading{
    color: white;
    text-shadow: 3px 4px 7px rgba(81,67,21,0.8);
    font-family: 'Raleway', sans-serif;
    margin: 16px ;
}

.search-bar{
    width: 50%;
    height: 20px;
    text-align: center;
    margin: 10px 330px;
    background-color: transparent;
    font-size: 20px;
    border: 1px solid rgb(255,255,255,0.5);
    padding: 5px 0;
    border-radius: 5px;
    outline: none;
}
